import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LibViewChildComponent } from './lib-view-child.component';


const routes: Routes = [
  {
    path: '',
    component: LibViewChildComponent
  },
  {
    path: 'content-child',
    loadChildren: 'app/feature/libs/lib-view-child/content-child/content-child.module#ContentChildModule'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LibViewChildRoutingModule { }



